.row {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.grid {
  display: grid;
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 10px;
}

.title {
  font-size: 1rem;
  font-weight: 600;
}

.small {
  font-size: 0.8rem;
}

.card {
  width: 230px;
  height: 490px;
}

.year_input {
  text-align: center;
}

.inputs {
  display: flex;
  justify-content: center;
}

.title {
  width: 250px;
}

.centered {
  display: flex;
  flex-direction: column;
  width: 500px;
}

.year-input {
  text-align: center;
}

.filter-item {
  margin: 5px;
}

.little-margin {
  margin-left: 50px;
}

.movie-container {
  height: 500px;
  margin: 50px;
  display: flex;
  justify-content: space-around;
}

.movie-container img {
  width: 305px;
  box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2);
}

.base-line {
  display: flex;
}

.info-container {
  width: 750px;
}